<template>
  <div class="music-list">
    <div class="back"  @click="back">
      <i class="icon-back"></i>
    </div>
    <h1 class="title" v-html="title" ref="title"></h1>
    <div class="bg-image" :style="bgStyle" ref="bgImage">
      <div class="play-wrapper">
        <div class="play" @click="randomAll" v-show="songs.length>0" ref="playBtn">
          <i class="icon-play"></i>
          <span class="text">随机播放全部</span>
        </div>
      </div>
      <div class="filter" ref="filter"></div>
    </div>
    <div class="bg-layer" ref="layer"></div>
    <scroll class="list" :data="songs" :listen-scroll="listenScroll" :probe-type="probeType"  @scroll="scroll" ref="list">
      <div class="song-list-wrapper">
        <songs-list :songs="songs" @select="selectItem" :rank="rank"></songs-list>
      </div>
    </scroll>
    <div class="loading-container" v-show="!songs.length">
      <loading></loading>
    </div>
  </div>
</template>

<script>
  import Scroll from 'base/scroll/scroll'
  import SongsList from 'base/song-list/song-list'
  import { prefixStyle } from 'common/js/dom'
  import loading from 'base/loading/loading'
  import { mapActions } from 'vuex'
  import { playListMixin } from 'common/js/mixin'
  const transform = prefixStyle('transform')
  const backdrop = prefixStyle('backdrop-filter')
  export default {
    mixins: [playListMixin],
    data() {
      return {
        scrollY: ''
      }
    },
    props: {
      title: {
        type: String,
        default: ''
      },
      bgImage: {
        type: String,
        default: ''
      },
      songs: {
        type: Array,
        default: []
      },
      rank: {
        type: Boolean,
        default: false
      }
    },
    created() {
      this.probeType = 3
      this.listenScroll = true
    },
    computed: {
      bgStyle() {
        return `background-image:url(${this.bgImage})`
      }
    },
    mounted() {
      this.RESERVED_HEIGHT = this.$refs.title.clientHeight; // title高度
      this.imageHeight = this.$refs.bgImage.clientHeight; // 背景高度
      this.$refs.list.$el.style.top = `${this.imageHeight}px`; // 设置list的top偏移量
      this.minTransalteY = -this.imageHeight + this.RESERVED_HEIGHT
    },
    methods: {
      handlePlayList(playlist) {
        const bottom = playlist.length > 0 ? '60px' : ''
        this.$refs.list.$el.style.bottom = bottom
        this.$refs.list.refresh()
      },
      back() {
        this.$router.back();
      },
      scroll(pos) {
        this.scrollY = pos.y
      },
      ...mapActions([
        'setPlay',
        'setRandomPlayAll'
      ]),
      selectItem(item, index) {
        this.setPlay({
          list: this.songs,
          index
        })
      },
      randomAll() {
        this.setRandomPlayAll({list: this.songs});
      }
    },
    watch: {
      scrollY(newY) {
        let transLateY = Math.max(this.minTransalteY, newY);
        let zIndex = 0;
        let scale = 1;
        let blur = 0;
        this.$refs.layer.style[transform] = `translate3d(0,${transLateY}px,0)`
        const percent = Math.abs(newY / this.imageHeight)
        if (newY > 0) {
          scale = 1 + percent;
          zIndex = 10
        } else {
          blur = Math.min(20, percent * 20)
        }
        this.$refs.filter.style[backdrop] = `blur(${blur}px)`
        // 设置最大滚动高度
        if (newY < this.minTransalteY) {
          zIndex = 10;
          this.$refs.bgImage.style.paddingTop = 0
          this.$refs.bgImage.style.height = this.RESERVED_HEIGHT + 'px'
          this.$refs.playBtn.style.display = 'none'
        } else {
          this.$refs.bgImage.style.paddingTop = '70%'
          this.$refs.bgImage.style.height = 0
          this.$refs.playBtn.style.display = ''
        }
        this.$refs.bgImage.style.zIndex = zIndex;
        this.$refs.bgImage.style[transform] = `scale(${scale})`
      }
    },
    components: {
      Scroll,
      SongsList,
      loading
    }
  }
</script>

<style lang="scss">
  @import "../../common/style/variable.scss";
  @import "../../common/style/mixin.scss";
  .music-list {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: $color-background;
    .back {
      position: absolute;
      top: 0;
      left: 6px;
      z-index: 50;
      .icon-back {
        display: block;
        padding: 10px;
        font-size: $font-size-large-x;
        color: $color-theme;
      }
    }
    .title {
      position: absolute;
      top: 0;
      left: 10%;
      z-index: 40;
      width: 80%;
      @include no-wrap();
      text-align: center;
      line-height: 40px;
      font-size: $font-size-large;
      color: $color-text;
      font-weight:normal;
    }
    .bg-image {
      position: relative;
      width: 100%;
      height: 0;
      padding-top: 70%;
      transform-origin: top;
      background-size: cover;
      .play-wrapper {
        position: absolute;
        bottom: 20px;
        z-index: 50;
        width: 100%;
        .play {
          box-sizing: border-box;
          width: 135px;
          padding: 7px 0;
          margin: 0 auto;
          text-align: center;
          border: 1px solid $color-theme;
          color: $color-theme;
          border-radius: 100px;
          font-size: 0;
          .icon-play {
            display: inline-block;
            vertical-align: middle;
            margin-right: 6px;
            font-size: $font-size-medium-x;
          }
          .text {
            display: inline-block;
            vertical-align: middle;
            font-size: $font-size-small;
          }
        }
      }
      .filter {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(7, 17, 27, 0.4);
      }
    }
    .bg-layer {
      position: relative;
      height: 100%;
      background: $color-background;
    }
    .list {
      position: fixed;
      top: 0;
      bottom: 0;
      width: 100%;
      background: $color-background;
      .song-list-wrapper {
        padding: 20px 30px;
      }
    }
    .loading-container {
      position: absolute;
      width: 100%;
      top: 60%;
      transform: translateY(-50%);
    }
  }
</style>




